import {Zombie} from "./Zombie";
import {simpleZombieImage} from "../imgComponents/simpleZombieImage";

class SimpleZombie extends Zombie{
    constructor(context) {
        super(context, 1, 10, 1);
        this.positionX = 0;
        this.positionY = 0;
        this.width = 166;
        this.height = 144;
        this.frame = 0;
        this.frameWaiting = 0;
        this.frameSpeed = 0;
    }

    positionOfCreate() {
        this.positionX = this.setPositionOfCreate(800, 900);
        this.positionY = this.setPositionOfCreate(100, 500);
    }

    create() {
        this.context.drawImage(simpleZombieImage.simpleZombieWaiting, this.frameWaiting, 0, 91, 155, this.positionX, this.positionY, 91, 155);
        this.frameSpeed++;
        if (this.frameSpeed === 4) {
            this.frameWaiting += 91;
            this.frameSpeed = 0;
        }
        if (this.frameWaiting > 1400) {
            this.frameWaiting = 0;
        }
    }

    setPositionOfCreate(min, max) {
        return Math.floor(Math.random() * (max - min +1)) + min;
    }

    walk() {
        this.context.drawImage(simpleZombieImage.simpleZombie, this.frame, 0, this.width, this.height, this.positionX, this.positionY, this.width, this.height);
        this.positionX -= 0.5;
        this.frameSpeed++;
        if (this.frameSpeed === 5) {
            this.frame += 166;
            this.frameSpeed = 0;
        }
        if (this.frame > 2987) {
            this.frame = 0;
        }
    }

    zombiesDead() {
        if (this.health === 0) {
            return true;
        }
    }
}

export { SimpleZombie }


