import {Plants} from "./plants";
import {sunflowerImages} from "../imgComponents/sunflowerImage";

class Sunflower extends Plants {
    constructor(context) {
        super(0, 5, 10, 50);
        this.startX = 0;
        this.logo = sunflowerImages.sunflowerLogo;
        this.startY = 0;
        this.endY = 0;
        this.context = context;
    }

    award() {
        this.context.drawImage(sunflowerImages.sunflowerLogo, this.startX, this.startY);
    }

    createAwardPosition(pointX, pointY) {
        this.startX = pointX;
        this.startY = pointY;
        this.endY = pointY - 10;
    }
}

export {Sunflower}


