class Zombie {
    constructor(context, speed, health, damage) {
        this.context = context;
        this.speed = speed;
        this.health = health;
        this.damage = damage;
    }

    create(unit, positionX, positionY) {

        this.context.drawImage(unit, positionX, positionY);
    }
}

export { Zombie }


